import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Pagination } from 'swiper/modules';
import 'swiper/css/bundle'; // Carga todos los estilos de Swiper de una vez
import { FaBookOpen, FaCalendarAlt, FaShare, FaHeart, FaTimes } from 'react-icons/fa';
import './reflexiones.css';

// Mover las reflexiones a un archivo separado o API para reducir el tamaño del bundle
const initialReflexiones = [{
  id: 1,
  title: "La Fe en Tiempos Difíciles",
  image: "https://images.unsplash.com/photo-1510422852126-9017d4b93f99?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8NHx8cmVmbGV4aW9uZXN8ZW58MHx8MHx8fDA%3D",
  excerpt: "Cómo mantener nuestra fe firme cuando enfrentamos adversidades y pruebas en la vida...",
  content: `<p>En los momentos más oscuros de nuestra vida, cuando las tormentas parecen no tener fin, es cuando nuestra fe es puesta a prueba. La Biblia nos recuerda en <strong>Hebreos 11:1</strong> que "la fe es la certeza de lo que se espera, la convicción de lo que no se ve".</p>
            <p>Jesús mismo nos enseñó que en este mundo tendríamos aflicción, pero que Él ha vencido al mundo (Juan 16:33). Cuando pasamos por dificultades, no estamos solos. Cristo camina con nosotros, llevando nuestras cargas y fortaleciéndonos en el proceso.</p>
            <p>La fe no es la ausencia de preguntas o dudas, sino la decisión de confiar en Dios a pesar de ellas. Como Job, podemos llegar a decir: "Aunque él me mate, en él esperaré" (Job 13:15).</p>
            <p><strong>Reflexión práctica:</strong> Hoy, identifica una área de tu vida donde necesitas ejercitar tu fe. Entrega esa situación a Dios en oración y confía en que Él obra incluso cuando no puedes ver el resultado.</p>`,
  date: "15 Marzo 2023",
  verse: "Hebreos 11:1",
  author: "Pastor Juan Pérez",
  isFavorite: false,
  tags: ["Fe", "Adversidad", "Esperanza"]
},
{
  id: 2,
  title: "El Poder del Perdón",
  image: "https://wallpapers.com/images/hd/reflection-pictures-n9784sj1jjkaen2c.jpg",
  excerpt: "Explorando la liberación que encontramos cuando elegimos perdonar como Cristo nos perdonó...",
  content: `<p>El perdón es uno de los conceptos más transformadores del cristianismo, pero también uno de los más difíciles de practicar. <strong>Colosenses 3:13</strong> nos exhorta: "Sopórtense unos a otros y perdónense si alguno tiene una queja contra otro. Así como el Señor los perdonó, perdonen también ustedes".</p>
            <p>Perdonar no significa aprobar el mal o olvidar el daño, sino liberarnos de la carga del resentimiento. Cuando perdonamos, seguimos el ejemplo de Jesús en la cruz, quien dijo: "Padre, perdónalos, porque no saben lo que hacen" (Lucas 23:34).</p>
            <p>El perdón es un proceso que a veces requiere tiempo y repetición. Pedro preguntó a Jesús si debía perdonar hasta siete veces, y Jesús le respondió: "No te digo hasta siete veces, sino hasta setenta veces siete" (Mateo 18:22).</p>
            <p><strong>Reflexión práctica:</strong> ¿Hay alguien a quien necesites perdonar hoy? Da el primer paso en oración, pidiendo a Dios que te ayude a soltar el resentimiento y a desear el bien para esa persona.</p>`,
  date: "22 Marzo 2023",
  verse: "Colosenses 3:13",
  author: "Pastora María González",
  isFavorite: false,
  tags: ["Perdón", "Liberación", "Relaciones"]
},
{
  id: 3,
  title: "Confianza en Medio de la Incertidumbre",
  image: "https://images.unsplash.com/photo-1506748686214-e9df14d4d9d0?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3",
  excerpt: "Cómo aprender a confiar en los planes de Dios cuando no entendemos lo que está pasando...",
  content: `<p>La incertidumbre es parte inevitable de la vida. No siempre sabremos qué sucederá mañana, pero sí podemos decidir en quién confiamos hoy. <strong>Proverbios 3:5-6</strong> dice: "Confía en el Señor con todo tu corazón y no te apoyes en tu propia prudencia; reconócelo en todos tus caminos y él enderezará tus sendas".</p>
            <p>Cuando las cosas no tienen sentido, es la oportunidad perfecta para aferrarnos a la promesa de que Dios tiene pensamientos de paz y no de mal para nosotros (Jeremías 29:11).</p>
            <p>Confiar no es ignorar la realidad, sino creer que Dios está obrando, incluso en medio del caos.</p>
            <p><strong>Reflexión práctica:</strong> Haz una lista de las cosas que te preocupan hoy y ora por ellas, entregándolas a Dios y recordando que sus caminos son mejores que los tuyos.</p>`,
  date: "30 Marzo 2023",
  verse: "Proverbios 3:5-6",
  author: "Pastor Daniel López",
  isFavorite: false,
  tags: ["Confianza", "Incertidumbre", "Fe"]
},
{
  id: 4,
  title: "El Valor de la Gratitud",
  image: "https://images.unsplash.com/photo-1506784983877-45594efa4cbe?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3",
  excerpt: "Descubriendo cómo la gratitud transforma nuestro corazón y nuestra perspectiva de la vida...",
  content: `<p>Ser agradecidos no es algo natural, especialmente en tiempos difíciles, pero es una de las actitudes más poderosas que podemos cultivar. <strong>1 Tesalonicenses 5:18</strong> nos instruye: "Den gracias en todo, porque esta es la voluntad de Dios para ustedes en Cristo Jesús".</p>
            <p>La gratitud cambia nuestra mirada. Nos ayuda a enfocarnos en lo que sí tenemos y en las bendiciones que Dios nos da cada día.</p>
            <p>Aun en medio de las pruebas, siempre hay algo por lo cual agradecer. Esa actitud nos fortalece y trae paz al corazón.</p>
            <p><strong>Reflexión práctica:</strong> Tómate unos minutos para anotar al menos 5 cosas por las que estás agradecido hoy y dale gracias a Dios por cada una de ellas.</p>`,
  date: "5 Abril 2023",
  verse: "1 Tesalonicenses 5:18",
  author: "Hermana Laura Ramírez",
  isFavorite: false,
  tags: ["Gratitud", "Actitud", "Esperanza"]
},
{
  id: 5,
  title: "El Propósito del Dolor",
  image: "https://wallpapers.com/images/hd/reflection-pictures-ycueyvnthqzajkeo.jpg",
  excerpt: "Comprendiendo cómo Dios usa nuestras dificultades para formar nuestro carácter y fe...",
  content: `<p>Nadie desea el dolor, pero Dios lo permite con un propósito. <strong>Romanos 8:28</strong> dice: "Sabemos que a los que aman a Dios, todas las cosas les ayudan a bien, esto es, a los que conforme a su propósito son llamados".</p>
            <p>A través del sufrimiento, Dios nos enseña, fortalece y prepara para cosas mayores. Como el oro se purifica en el fuego, así nuestra fe crece en la prueba.</p>
            <p>El dolor no es el final de la historia; es un medio para acercarnos más a Dios y descubrir su fidelidad.</p>
            <p><strong>Reflexión práctica:</strong> Piensa en una prueba que hayas enfrentado y cómo Dios te ayudó a superarla. Escribe lo que aprendiste de esa experiencia.</p>`,
  date: "12 Abril 2023",
  verse: "Romanos 8:28",
  author: "Pastor Andrés Morales",
  isFavorite: false,
  tags: ["Dolor", "Propósito", "Esperanza"]
},
{
  id: 6,
  title: "La Paz que Sobrepasa Todo Entendimiento",
  image: "https://images.unsplash.com/photo-1507525428034-b723cf961d3e?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3",
  excerpt: "Cómo experimentar la paz de Dios, incluso en medio de las tormentas de la vida...",
  content: `<p>La paz de Dios no depende de las circunstancias externas, sino de su presencia interna. <strong>Filipenses 4:7</strong> afirma: "Y la paz de Dios, que sobrepasa todo entendimiento, guardará sus corazones y sus pensamientos en Cristo Jesús".</p>
            <p>Cuando entregamos nuestras cargas a Dios en oración, Él responde con una paz inexplicable que nos sostiene incluso en medio del dolor o la incertidumbre.</p>
            <p>Esa paz nos permite dormir en medio de la tormenta, confiar cuando no hay respuestas y esperar con esperanza.</p>
            <p><strong>Reflexión práctica:</strong> Dedica unos minutos hoy a orar y entregarle a Dios todo lo que te inquieta, pidiéndole que su paz llene tu corazón.</p>`,
  date: "19 Abril 2023",
  verse: "Filipenses 4:7",
  author: "Hermana Sofía Díaz",
  isFavorite: false,
  tags: ["Paz", "Oración", "Esperanza"]
}]; 

function Reflexiones() {
  const [reflexiones, setReflexiones] = useState(initialReflexiones);
  const [selectedReflexion, setSelectedReflexion] = useState(null);

  // Memoizar las reflexiones para evitar renders innecesarios
  const memoizedReflexiones = useMemo(() => reflexiones, [reflexiones]);

  // Cargar favoritos desde localStorage
  useEffect(() => {
    const savedFavorites = localStorage.getItem('reflexionesFavoritas');
    if (savedFavorites) {
      const favoritos = JSON.parse(savedFavorites);
      setReflexiones(prev =>
        prev.map(r => ({ ...r, isFavorite: favoritos.includes(r.id) }))
      );
    }
  }, []);

  // Memoizar la función toggleFavorite
  const toggleFavorite = useCallback((id) => {
    setReflexiones(prev => {
      const updated = prev.map(r =>
        r.id === id ? { ...r, isFavorite: !r.isFavorite } : r
      );

      if (selectedReflexion?.id === id) {
        setSelectedReflexion(updated.find(r => r.id === id));
      }

      const favoritos = updated.filter(r => r.isFavorite).map(r => r.id);
      localStorage.setItem('reflexionesFavoritas', JSON.stringify(favoritos));

      return updated;
    });
  }, [selectedReflexion]);

  // Memoizar la función shareReflexion
  const shareReflexion = useCallback((reflexion) => {
    const shareUrl = window.location.href.includes('localhost')
      ? 'http://localhost:3000/#reflexiones'
      : 'https://iglesiapiedrasvivas.github.io/#reflexiones';

    const shareText = `🙏 *${reflexion.title}* 🙏\n\n${stripHtml(reflexion.content)}\n\n💬 *Versículo clave:* ${reflexion.verse}\n\n✨ *Reflexión completa:* ${shareUrl}\n\n#ReflexiónCristiana #Fe #Devocional`;

    if (navigator.share) {
      navigator.share({
        title: reflexion.title,
        text: shareText,
        url: shareUrl,
      }).catch(console.error);
    } else {
      const shareWindow = window.open('', '_blank', 'width=500,height=400');
      shareWindow.document.write(getShareWindowHTML(shareText, shareUrl));
      shareWindow.document.close();
    }
  }, []);

  // Función para extraer texto de HTML
  const stripHtml = (html) => {
    const doc = new DOMParser().parseFromString(html, 'text/html');
    return doc.body.textContent || '';
  };

  // Función para generar HTML de la ventana de compartir (memoizable)
  const getShareWindowHTML = (shareText, shareUrl) => `
    <!DOCTYPE html>
    <html>
      <head>
        <title>Compartir Reflexión</title>
        <style>
          body { font-family: Arial, sans-serif; padding: 20px; }
          button {
            display: block; width: 100%; padding: 10px; margin: 10px 0;
            background: #f0f0f0; border: 1px solid #ddd; border-radius: 5px;
            cursor: pointer;
          }
          button:hover { background: #e0e0e0; }
        </style>
      </head>
      <body>
        <h2>Compartir reflexión</h2>
        <button onclick="window.open('https://wa.me/?text=${encodeURIComponent(shareText)}', '_blank')">WhatsApp</button>
        <button onclick="window.open('https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(shareUrl)}&quote=${encodeURIComponent(shareText)}', '_blank')">Facebook</button>
        <button onclick="window.open('https://twitter.com/intent/tweet?text=${encodeURIComponent(shareText)}', '_blank')">Twitter</button>
        <button onclick="navigator.clipboard.writeText('${shareText.replace(/'/g, "\\'")}'); alert('Mensaje copiado');">Copiar enlace</button>
        <button onclick="window.close()">Cancelar</button>
      </body>
    </html>
  `;

  // Función para abrir reflexión (memoizable)
  const openReflexion = useCallback((reflexion) => {
    setSelectedReflexion(reflexion);
    document.body.style.overflow = 'hidden';
  }, []);

  // Función para cerrar reflexión (memoizable)
  const closeReflexion = useCallback(() => {
    setSelectedReflexion(null);
    document.body.style.overflow = 'auto';
  }, []);

  return (
    <section className="reflexiones-section" id="reflexiones">
      <div className="container py-3 px-0 shadow-lg">
        <div className="section-header text-center">
          <h2 className="section-title mb3">Reflexiones Diarias</h2>
          <p className="section-subtitle mb-0 mt-2">Alimento espiritual para tu caminar con Cristo</p>
        </div>

        <Swiper
          modules={[Autoplay, Navigation, Pagination]}
          spaceBetween={30}
          slidesPerView={1}
          loop={true}
          autoplay={{ delay: 5000, disableOnInteraction: false }}
          pagination={{ clickable: true }}
          navigation={true}
          breakpoints={{
            576: { slidesPerView: 2 },
            992: { slidesPerView: 3 }
          }}
          className="reflexiones-swiper"
        >
          {memoizedReflexiones.map((reflexion) => (
            <SwiperSlide key={reflexion.id}>
              <ReflexionCard
                reflexion={reflexion}
                onOpen={openReflexion}
                onShare={shareReflexion}
                onToggleFavorite={toggleFavorite}
              />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      {selectedReflexion && (
        <ReflexionModal
          reflexion={selectedReflexion}
          onClose={closeReflexion}
          onShare={shareReflexion}
          onToggleFavorite={toggleFavorite}
        />
      )}
    </section>
  );
}

// Componente separado para la tarjeta de reflexión (mejor rendimiento)
const ReflexionCard = React.memo(({ reflexion, onOpen, onShare, onToggleFavorite }) => (
  <div className="reflexion-card">
    <div className="reflexion-image-container">
      <img
        src={reflexion.image}
        alt={reflexion.title}
        className="reflexion-image"
        loading="lazy"
      />
      <button
        className={`favorite-btn ${reflexion.isFavorite ? 'active' : ''}`}
        onClick={() => onToggleFavorite(reflexion.id)}
        aria-label={reflexion.isFavorite ? 'Quitar de favoritos' : 'Añadir a favoritos'}
      >
        <FaHeart />
      </button>
    </div>
    <div className="reflexion-content">
      <div className="reflexion-meta mb-0">
        <span className="reflexion-date">
          <FaCalendarAlt className="me-2" />
          {reflexion.date}
        </span>
        <span className="reflexion-verse">
          <FaBookOpen className="me-2" />
          {reflexion.verse}
        </span>
      </div>
      <h3 className="reflexion-title" style={{ height: "50px", marginBottom: "5px" }}>
        {reflexion.title}
      </h3>
      <p className="reflexion-excerpt">{reflexion.excerpt}</p>
      <div className="reflexion-actions">
        <button className="btn btn-reflexion" onClick={() => onOpen(reflexion)}>
          Leer más
        </button>
        <button className="btn btn-share" onClick={() => onShare(reflexion)}>
          <FaShare className="me-2" />
          Compartir
        </button>
      </div>
    </div>
  </div>
));

// Componente separado para el modal (mejor rendimiento)
const ReflexionModal = React.memo(({ reflexion, onClose, onShare, onToggleFavorite }) => (
  <div className="reflexion-modal">
    <div className="reflexion-modal-overlay" onClick={onClose}></div>
    <div className="reflexion-modal-content">
      <button className="reflexion-modal-close" onClick={onClose}>
        <FaTimes />
      </button>

      <div className="reflexion-modal-header">
        <img
          src={reflexion.image}
          alt={reflexion.title}
          className="reflexion-modal-image"
          loading="lazy"
        />
        <div className="reflexion-modal-header-content">
          <h2>{reflexion.title}</h2>
          <div className="reflexion-modal-meta">
            <span><FaCalendarAlt /> {reflexion.date}</span>
            <span><FaBookOpen /> {reflexion.verse}</span>
            {reflexion.author && <span>Por: {reflexion.author}</span>}
          </div>
          <div className="reflexion-modal-tags">
            {reflexion.tags?.map(tag => (
              <span key={tag} className="reflexion-tag">{tag}</span>
            ))}
          </div>
        </div>
      </div>

      <div
        className="reflexion-modal-body"
        dangerouslySetInnerHTML={{ __html: reflexion.content }}
      ></div>

      <div className="reflexion-modal-footer">
        <button
          className={`btn-favorite ${reflexion.isFavorite ? 'active' : ''}`}
          onClick={() => onToggleFavorite(reflexion.id)}
        >
          <FaHeart className={reflexion.isFavorite ? 'text-danger' : ''} />
          {reflexion.isFavorite ? 'En favoritos' : 'Añadir a favoritos'}
        </button>
        <button className="btn-share-modal" onClick={() => onShare(reflexion)}>
          <FaShare /> Compartir reflexión
        </button>
      </div>
    </div>
  </div>
));

export default React.memo(Reflexiones);